.navbar{
  position: -webkit-sticky;
  position: sticky;
  top: 0;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-line-pack: center;
      align-content: center;
  -ms-flex-pack: end;
      justify-content: flex-end;
  background-color: black;
  width: 100%;
  z-index: 10;
  text-transform: uppercase;
  overflow: hidden;
}

.navbar-link {
  padding: 1em;
  text-decoration: none;
  font-size:calc(9px + 0.5vw); /*sets lower limit on font */
  line-height: 1em;
  color: var(--off-white);
}


.navbar-link-active, 
.navbar-link:hover{
  color: var(--accent-color-1);
  cursor: pointer;
}

a:active,
a:focus {
  outline:none;
}

.navbar a:hover{
  text-decoration: none;
}

.hidden {
  display: none;
}


