.projects-section {
  padding: 4em 2em;
  -webkit-box-shadow: 0 5px 10px rgba(0,0,0,0.25);
          box-shadow: 0 5px 10px rgba(0,0,0,0.25);
}

.projects-section h1, h2, h3{
  color: var(--black);
  margin: 0;
}

.projects-container {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-flow: row wrap;
      flex-flow: row wrap;
  -ms-flex-pack: center;
      justify-content: center;
  padding: 4em 2em 0 2em;
}

@media screen and ( max-width: 600px ){
  .projects-container{
    -ms-flex-flow: column nowrap;
        flex-flow: column nowrap;
    -ms-flex-align: center;
        align-items: center;
  }
}

.projects-container:nth-of-type(4n) {
	margin-right: 0;	
}

.projects-container:nth-of-type(4n+1) {
	margin-left: 0;	
}

.coming-soon {
  text-transform: uppercase;
}


