@import url('https://fonts.googleapis.com/css?family=Architects+Daughter');
.brand {
  -ms-flex-item-align: start;
      align-self: flex-start;
  width: 200px;
  height: 200px;
  top: -5.1em;
  left: -5em;
  position: absolute;
  -webkit-transform: scale(0.7);
      -ms-transform: scale(0.7);
          transform: scale(0.7);
}

.brand:before {
  background-size: 100% 100%;
  content: "PG.";
  opacity: .8;
  font-weight: 400;
  line-height: 200px;
  color: var(--accent-color-1);
  font-size: 2.2em;
  font-family: "Architects Daughter", "Roboto", "Open Sans", sans-serif;
  text-align: center;
  width: 200px;
  height: 200px;
  margin: 0 4px;
  position: absolute;
  z-index: 99999 !important;
}

.logo{
  -webkit-animation: 5s linear infinite;
          animation: 5s linear infinite;
  -webkit-animation-name: rotate-0;
          animation-name: rotate-0;
  -webkit-animation-delay: 0s;
          animation-delay: 0s;
  -webkit-transform-origin: 50% 50%;
      -ms-transform-origin: 50% 50%;
          transform-origin: 50% 50%;
  -webkit-transform: rotate(0deg);
      -ms-transform: rotate(0deg);
          transform: rotate(0deg);
}


.logo > svg > path{
  positon:absolute;
  fill:none;
  top:0;
  left:0;
  mix-blend-mode: multiply;
  -webkit-transform-origin: 50% 50%;
      -ms-transform-origin: 50% 50%;
          transform-origin: 50% 50%;
  -webkit-transform: rotate(0deg) ;
      -ms-transform: rotate(0deg) ;
          transform: rotate(0deg) ;
  -webkit-animation: 20s linear infinite;
          animation: 20s linear infinite;
}

.logo svg path:nth-child(1) {
  opacity: .8;
  stroke-width: 3;
  -webkit-animation-name: rotate-1;
          animation-name: rotate-1;
  -webkit-animation-delay: .5s;
          animation-delay: .5s;
}
.logo svg path:nth-child(2) {
  opacity: .6;
  stroke-width: 2;
  -webkit-animation-name: rotate-2;
          animation-name: rotate-2;
  -webkit-animation-delay: .6s;
          animation-delay: .6s;
}
.logo svg path:nth-child(3) {
  opacity: .8;
  stroke-width: 3;
  -webkit-animation-name: rotate-3;
          animation-name: rotate-3;
  -webkit-animation-delay: .7s;
          animation-delay: .7s;
}


@-webkit-keyframes scale-1 {
  0%{
    -webkit-transform: scale(1);
            transform: scale(1);
  }50% {
    -webkit-transform: scale(.9);
            transform: scale(.9);
  }100% {
    -webkit-transform: scale(1);
            transform: scale(1);
  } 
}


@keyframes scale-1 {
  0%{
    -webkit-transform: scale(1);
            transform: scale(1);
  }50% {
    -webkit-transform: scale(.9);
            transform: scale(.9);
  }100% {
    -webkit-transform: scale(1);
            transform: scale(1);
  } 
}

@-webkit-keyframes rotate-0 {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
    to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

@keyframes rotate-0 {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
    to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
} 

@-webkit-keyframes rotate-1 {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
    to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
} 

@keyframes rotate-1 {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
    to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

@-webkit-keyframes rotate-2 {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
    to { -webkit-transform: rotate(-360deg); transform: rotate(-360deg); }
}

@keyframes rotate-2 {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
    to { -webkit-transform: rotate(-360deg); transform: rotate(-360deg); }
}

@-webkit-keyframes rotate-3 {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
    to { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
}

@keyframes rotate-3 {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
    to { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
}


