footer{
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  margin-top: 1.5em;
}

.icons {
  width: calc(40px + 1vw);
  height: calc(40px + 1vw);
  margin: 0.5em 0.5em 0 0.5em;
}

.icons svg:hover #circle{
  fill: var(--accent-color-1);
  cursor: pointer;
}

.icons svg #circle {
  fill: var(--black);
  -webkit-transition: 700ms;
  -o-transition: 700ms;
  transition: 700ms;
}

.icon-wrapper {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
  -ms-flex-pack: justify;
      justify-content: space-between;;
  -ms-flex-align: center;;
      align-items: center;
  margin-top: 0.6em;
  -ms-flex-order: 1;
      order: 1;
}

#trademark {
  -ms-flex-order: 2;
      order: 2;
  margin: 1em;
}



